import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  CAlert,
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CLink,
  CProgress,
  CRow,
} from "@coreui/react";
// import { DocsLink } from 'src/reusable'

const Alerts = () => {
  const [item, SetItem] = useState([]);
  const [itema, SetItema] = useState([]);
  function getRandomColor() {
    let cols = ["#4CFA6C", "#FF79C6", "#BD85C8", "#F1FFDF","#AF93F9"]
    const item = cols[Math.floor(Math.random() * cols.length)]
    console.log(item)
    return item
  }
  useEffect(() => {
    let ServicesConfig = {
      method: "get",
      url: `http://46.209.219.53:5003/home/getAllItemFromColdDb`,
    };
    axios(ServicesConfig)
      .then((Response) => {
        if (Response.status === 200) {
          // console.log(Response.data[0]);
          SetItem(Response.data[0]);
        }
      })
      .catch((error) => {
        console.log(error);
      });
    let ServicesConfiga = {
      method: "get",
      url: `http://46.209.219.53:5003/home/getAllItemFromNotifyDb`,
    };
    axios(ServicesConfiga)
      .then((Response) => {
        if (Response.status === 200) {
          console.log(Response);
          SetItema(Response.data);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <>
      <CRow>
        <CCol xs="12" md="6">
          <CCard>
            <CCardHeader>
              اطلاعات
              <small> </small>
            </CCardHeader>
            <CCardBody style={{ backgroundColor: "#282A36" }}>
              <CAlert style={{ direction:'ltr' }} >
                <h4 className="alert-heading"></h4>
                <p style={{ color: getRandomColor() }}>{item[0]}</p>
                <p style={{ color: getRandomColor() }}>{item[1]}</p>
                <p style={{ color: getRandomColor() }}>{item[2]}</p>
                <p style={{ color: getRandomColor() }}>{item[3]}</p>
                <p style={{ color: getRandomColor() }}>{item[4]}</p>
                <p style={{ color: getRandomColor() }}>{item[5]}</p>
                <p style={{ color: getRandomColor() }}>{item[6]}</p>
                <p style={{ color: getRandomColor() }}>{item[7]}</p>
                <hr />
              </CAlert>
            </CCardBody>
          </CCard>
        </CCol>
        <CCol xs="12" md="6">
          <CCard>
            <CCardHeader>
              اطلاعات
              <small> </small>
            </CCardHeader>
            <CCardBody style={{ backgroundColor: "#282A36" }}>
              <CAlert >
                <h4 className="alert-heading"></h4>
                {itema.map((items, index) => {
                  return (
                    <>
                      {items.map((subItems, sIndex) => {
                        return (
                        <p style={{ color: getRandomColor(), direction:'ltr' }}> {subItems} </p>);
                      })}
                    </>
                  );
                })}
                <hr />
              </CAlert>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  );
};

export default Alerts;
